import React, { Component } from 'react';
import { createApolloFetch } from 'apollo-fetch';
import { Row, Col, Card, Modal } from 'react-bootstrap';
import { Paper } from '@material-ui/core';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';

export default class TestCard extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    componentDidMount() {

        let deleteBtns = document.querySelectorAll('.btn-more-biographies');
        for (let i = 0; i < deleteBtns.length; i++) {
            deleteBtns[i].addEventListener("click", function () {
            //    // this.querySelector(".custom-list-biography").classList.toggle("d-none");
            //     this.querySelector('.delete-biography').addEventListener("click", function () {
            //             let bioId = this.querySelector('button').getAttribute('data-id');
            //     })
            });
        }
    }
    render() {
        const handleClose = () => this.setState({ ...this.state, show: false });
        const handleShow = () => this.setState({ ...this.state, show: true });
        if(!this.props.data[0]){
           return(
            <Row className="m-0 p-3 pt-4">
                <Col className="text-center text-grey" sm={12}>
                    <h5>You have no biographies</h5>
                </Col>
            </Row>
           )
        }
        else{
        return (
            <Row className="m-0 p-3 pt-4">
                {
                    this.props.data.map(bio => {
                        console.log(bio.questions[0] && bio.questions[0].video);
                        if (bio.questions[0] && bio.questions[0].video) {
                            return (
                                <Col className="p-3" md='4'>
                                    <Card className="bg-transparent text-dark border-0">
                                        <Paper elevation={3} className="play-icon-container">
                                            <button type="button" className="bg-transparent border-0 p-0 play-icon-container" onClick={handleShow}>
                                                <Card.Img src={bio.questions[0].video.frame.url} alt="Card image" />
                                                <div className="text-white play-icon">
                                                    <PlayCircleFilledIcon style={{ fontSize: '50px' }} />
                                                </div>
                                            </button>
                                        </Paper>
                                        <Modal
                                            size="lg"
                                            aria-labelledby="contained-modal-title-vcenter"
                                            centered
                                            className="row m-0 modal-video"
                                            show={this.state.show}
                                            onHide={handleClose}>
                                            <Col xs={12} className="p-0">
                                                <Modal.Body className="p-0">
                                                    {/* <div xs={6} className="text-left p-0 bold p-2 bg-white captialize"><h6>{bio.title}</h6></div> */}
                                                    <iframe title={bio.title} src={bio.questions[0].video.url} width="100%" height="377px"></iframe>
                                                </Modal.Body>
                                            </Col>
                                        </Modal>
                                        <Row className="m-0">
                                            <Col xs={6} className="text-left p-0 bold mt-3">{bio.title}</Col>
                                            {/* <Col xs={6} className="text-right p-0 bold">
                                            <button className="bg-transparent border-0 p-0"></button></Col> */}
                                            <Col xs={6} className="text-right p-0 bold">
                                                <button className="bg-transparent border-0 p-0 btn-more-biographies">
                                                    <p className="mb-0 mt-1 font-21">...</p>
                                                    <ul className="shadow custom-list-biography d-none">
                                                        <li className="list-style-none text-center delete-biography">
                                                            <button type="button" data-id={bio.id} className="bg-transparent border-0">
                                                                Delete
                                                            </button>
                                                        </li>
                                                    </ul>
                                                </button>
                                            </Col>
                                        </Row>
                                        <Row className="m-0 pt-1  text-grey">
                                            <Col xs={7} className="text-left p-0"><span className="text-orange">{bio.speciality.name_en} </span><br/>{bio.questions[0].video.created_at.substring(0, 10)}</Col>
                                            <Col xs={5} className="text-right p-0">{bio.questions[0].maximum_time} Sec</Col>
                                        </Row>
                                    </Card>
                                </Col>
                            )
                        }
                    })
                }
            </Row>
        )
    }
    }
}